.w-e-block {
  margin-top: 4vw;
  margin-bottom: 0;
  list-style: none;
  padding-left: 0;

  &__item {
    flex-basis: 48%;
    margin: 0 1% 2%;
    padding: 1.2rem 1.8rem;
    background-color: $bg_1;
    border-radius: .375rem;

    &--size-l {
      flex-basis: 60%;
    }

    &--bg-1 {
      background-image: url('../images/ad_bg_1.jpg');
      background-position: 100% 0;
      background-size: 7rem;
      background-repeat: no-repeat;
    }

    &--bg-2 {
      background-image: url('../images/ad_bg_2.jpg');
      background-position: 100% 0;
      background-size: 8rem;
      background-repeat: no-repeat;
    }
  }

  @media screen and (max-width: $mobile) {
    .btn {
      width: 100%;
    }
  }

  @media screen and (min-width: $tablet_s) {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    &__item {
      padding: 2rem 2.5rem;
    }

    &--bg-1 {
      background-size: 10rem;
    }

    &--bg-2 {
      background-size: contain;
    }
  }
}

.mobile-wrap {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.mobile {
  position: relative;
  flex-basis: 55%;
  border-radius: 50%;
  overflow: hidden;

  &__backdrop {
    display: block;
    width: 100%;
    padding-bottom: 100%;
    background-color: $gray_1;
  }

  &__img {
    position: absolute;
    left: 50%;
  }

  @media screen and (max-width: $desktop - 1) {
    &__img {
      bottom: 0;
      max-width: 16rem;
      margin-left: -8rem;
    }
  }

  @media screen and (max-width: $tablet - 1) {
    &__img {
      bottom: -3rem;
    }
  }

  @media screen and (min-width: $desktop) {
    &__img {
      position: absolute;
      bottom: 0;
      left: 50%;
      max-width: 20rem;
      margin-left: -10rem;
    }
  }
}

@media screen and (min-width: $desktop) {
  .gs-w-e-1,
  .gs-w-e-2,
  .gs-w-e-3 {
    opacity: 0;
  }

  .gs-mob {
    &-wrap {
      opacity: 0;
      transform: scale(0);
    }
  }
}
