
body {
  overflow-x: hidden;
}

.sub-nav {
  display: none;
  padding-left: 0;
  list-style: none;
  background-color: $white;
  transition: height .4s;

  &__item {
    display: inline-block;
    padding: .5rem 1.8rem;
    color: $blue_dark;
    text-decoration: none;
    font-size: .875rem;
    cursor: pointer;
    transition: color .3s;

    &:hover {
      color: $acccent;
    }
  }
}

.nav-list {
  display: flex;
  align-items: flex-start;
  margin: 0;
  padding: 3rem 0;
  list-style: none;

  > li {
    margin: 0 .3rem;
    padding: .7rem 0;
    color: $blue_dark;
    transition: color .3s;

    &.has-sub-nav {
      border-radius: 1rem;
      background-color: $white;
      position: relative;
      overflow: hidden;

      .nav-list__item::after {
        content: '';
        display: inline-block;
        width: .5rem;
        height: .5rem;
        margin-left: 1rem;
        border-left: 2px solid $acccent;
        border-bottom: 2px solid $acccent;
        transform: rotateZ(-45deg);
      }

      &:hover {
        color: $acccent;
        box-shadow: 0 0 40px 0 rgba(0, 28, 85, .08);

        .nav-list__item::after {
          transform: rotateZ(135deg);
        }

        .sub-nav {
          display: block;
        }
      }
    }

    &:hover {
      color: $acccent;
    }
  }

  &__item {
    display: inline-block;
    padding: .5rem 1.2rem;
    color: inherit;
    text-decoration: none;
    font-size: .875rem;
    letter-spacing: .05rem;
    cursor: pointer;
  }
}

.header {
  display: flex;
  align-items: center;
  width: 100%;

  &__logo {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 5rem;
    text-decoration: none;

    .text {
      margin-top: .3rem;
      max-width: 3.2rem;
    }
  }

  &-action-btn {
    margin-left: 2rem;
    margin-top: 2.8rem;
  }

  &.floating {
    height: 5.5rem;

    .header__logo {
      flex-direction: row;
    }

    .nav-list {
      padding: 1rem 0;
    }

    .header-action-btn {
      margin-top: .8rem;
    }
  }

  @media screen and (max-width: $desktop - 1) {
    height: 4rem;

    &__logo {
      flex-direction: row;
      width: 3rem;

      .text {
        max-width: 2.2rem;
      }
    }

    .nav-toggler {
      margin-left: auto;
    }

    &__nav {
      display: none;
    }
  }

  @media screen and (min-width: $desktop) {
    height: 9.5rem;

    .nav-toggler {
      display: none;
    }

    &__nav {
      display: flex;
      align-self: flex-start;
      align-items: flex-start;
      margin-left: auto;
    }

  }
}

.promo {
  position: relative;

  &__title {
    max-width: 23rem;
    margin-top: 0;
    margin-bottom: 1rem;
    font-size: 2.2rem;
    font-family: $title-font;
    line-height: 1.3;
    font-weight: bold;
    letter-spacing: .05rem;
  }

  &__text {
    max-width: 25rem;
    margin-top: 0;
    margin-bottom: 3.5rem;
    font-size: 1em;
    line-height: 1.5;
  }

  &__image {
    display: none;
  }

  @media screen and (max-width: $mobile - 1) {
    .btn {
      width: 100%;
    }
  }

  @media screen and (min-width: $tablet_s) {
    padding-bottom: calc(2vw + 2rem);

    &__title {
      padding-top: calc(5vw + 2rem);
    }

    &__text {
      margin-bottom: 3.5rem;
    }

    &__image-mob {
      display: none;
    }

    &__image {
      display: block;
      position: absolute;
      max-width: 50vw;
      top: 0;
      right: -4vw;
      z-index: -1;
    }
  }

  @media screen and (min-width: $desktop) {
    padding: 13rem 0 3rem;

    &__title {
      max-width: 28rem;
      margin-bottom: 3.5rem;
      padding-top: 0;
      font-size: 3.5rem;
    }

  }
}

.banner {
  padding: 3.5rem;
  background-color: $blue;
  background-repeat: no-repeat;
  text-align: center;

  &__title {
    max-width: 22rem;
    margin: 0 auto 2rem;
    color: $white;
    font-weight: bold;
    font-size: 2.25rem;
    font-family: $title-font;

    .light {
      font-weight: 400;
    }
  }

  @media screen and (max-width: $tablet - 1) {
    padding: 2rem;

    &__title {
      font-size: 1.8rem;
    }
  }

  @media screen and (max-width: $mobile - 1) {
    background-image: url('../images/banner_r.jpg');
    background-position: 100% 0;
    background-size: 12rem;
    border-radius: 1.5rem;

    &__title {
      max-width: 19rem;
      margin: 5rem 0 2rem;
      text-align: left;
    }

    .btn {
      width: 100%;
    }
  }

  @media screen and (min-width: $mobile) {
    background-image: url('../images/banner_l.jpg'), url('../images/banner_r.jpg');
    background-position: 0 50%, 100% 0;
    background-size: contain;
    border-radius: 3rem;
  }
}

.gs-header {
  @media screen and (min-width: $desktop) {
    position: fixed;
    left: 0;
    right: 0;
    background-color: $white;
    z-index: 3;
  }
}
